@import url(https://fonts.googleapis.com/css?family=Quicksand:300,400,700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Quicksand', sans-serif;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

ul {
  list-style: none;
}

p {
  font-size: 18px;
  color: #2e2e2e;
}

img {
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 700;
  color: #2e2e2e;
}

.logo {
  position: relative;
  height: 50px;
  width: 50px;
  background: #4CAF50;
  border-radius: 8px;
}

.logo .circle {
  position: absolute;
  bottom: 10px;
  right: 10px;
  width: 15px;
  height: 15px;
  background: #F44336;
  border-radius: 40px;
}

.brand-area {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  padding: 25px 0;
}

.brand-area p {
  color: #2e2e2e;
  font-size: 36px;
  margin-left: 5px;
  font-weight: 900;
  padding: 0 10px;
}

@media (max-width: 768px) {
  .brand-area {
    justify-content: center;
  }
}

.navbar {
  position: relative;
  z-index: 1;
}

.container {
  width: 1200px;
  margin: 0 auto;
  padding: 100px 0;
}

@media (max-width: 1199px) {
  .container {
    width: 100%;
    padding: 100px 25px;
  }
}

@media (max-width: 768px) {
  .container {
    padding: 60px 15px;
  }
}

.title {
  text-align: center;
  margin-bottom: 100px;
}

.title h2 {
  font-size: 48px;
  margin-bottom: 15px;
}

.title p {
  font-size: 24px;
}

@media (max-width: 768px) {
  .title {
    margin-bottom: 60px;
  }

  .title h2 {
    font-size: 36px;
  }

  .title p {
    font-size: 22px;
  }
}

button {
  border: none;
  background: #ebebeb;
  color: #fff;
  padding: 15px 25px;
  font-weight: 900;
  text-transform: uppercase;
  cursor: pointer;
}

input {
  padding: 10px 15px;
  border: solid 2px #bebebe;
  margin: 5px;
  font-size: 18px;
  border-radius: 4px;
  width: calc(100%/2 - 10px);
}

@media (max-width: 480px) {
  input {
    width: 100%;
  }
}

.alert {
  margin-top: 15px;
  font-size: 14px;
  text-transform: uppercase;
  font-weight: 700;
}

.alert.error {
  color: #F44336;
}

.alert.success {
  color: #4CAF50;
}

.marker {
  text-align: center;
  padding: 15px 25px;
}

.marker h4,
.marker p {
  color: #2e2e2e;
}

.marker h4 {
  font-size: 28px;
}

.intro-area {
  position: relative;
  background: #ecdb43;
  height: 700px;
}

.intro-area::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: url(/static/media/bg.b1c70438.png);
  background-size: cover;
  background-position: center;
  opacity: 1;
  z-index: 0;
}

.intro-area::after {
  content: "";
  position: absolute;
  bottom: -45px;
  left: calc(50% - 50px);
  height: 100px;
  background: #ecdb43;
  width: 100px;
  border-radius: 8px;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  z-index: -1;
}

.intro-area .info {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}

.intro-area .info>* {
  flex: 1 1;
}

.intro-area .info .animation {
  position: relative;
  text-align: left;
  flex: 1 1;
  z-index: 1;
}

.intro-area .info .animation img {
  position: relative;
  -webkit-animation: guyAnimated 1s alternate infinite;
          animation: guyAnimated 1s alternate infinite;
}

.intro-area .intro {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: center;
  height: 400px;
  z-index: 1;
  flex: 2 1;
}

.intro p {
  font-weight: 300;
  font-size: 24px;
}

.intro h1 {
  font-size: 52px;
}

.details {
  margin-top: 45px;
}

.details ul li {
  display: inline-block;
  margin-right: 25px;
}

.details ul li p {
  font-size: 26px;
  font-weight: 900;
}

.details ul li p small {
  display: block;
  font-weight: 300;
}

@media (max-width: 768px) {
  .intro-area .info {
    flex-direction: column-reverse;
  }

  .intro-area::after {
    bottom: -25px;
    left: calc(50% - 30px);
    height: 60px;
    width: 60px;
  }

  .intro {
    text-align: center;
  }

  .intro p,
  .details ul li p {
    font-size: 22px;
  }

  .intro h1 {
    font-size: 36px;
  }

  .intro-area .info .animation {
    margin: 25px 0;
  }

  .intro-area .info .animation img {
    width: 180px;
  }
}

@media (max-width: 480px) {
  .intro-area {
    height: 660px;
  }

  .intro-area .intro {
    margin: 0;
  }

  .intro-area .info .animation {
    margin: 15px 0;
  }

  .intro {
    height: auto;
    margin-top: 45px;
  }

  .details {
    margin: 0;
  }

  .intro p {
    font-size: 16px;
  }

  .intro h1 {
    font-size: 36px;
  }

  .details ul li {
    margin: 0 3px;
    margin-top: 15px;
  }

  .details ul li p {
    font-size: 18px;
  }
}

@-webkit-keyframes guyAnimated {
  from {
    top: 0;
  } to {
    top: -15px;
  }
}

@keyframes guyAnimated {
  from {
    top: 0;
  } to {
    top: -15px;
  }
}

.action-area {
  overflow: hidden;
}

.action {
  width: 640px;
  margin: 0 auto;
  text-align: center;
  z-index: 1;
}

.wait {
  text-align: center;
}

.wait img {
  height: 160px;
}

form {
  text-align: center;
  width: 640px;
  margin: 0 auto;
  margin-top: 25px;
}

@media (max-width: 768px) {
  .action {
    width: 100%;
    padding: 0 25px;
  }

  form {
    width: 100%;
    padding: 0 25px;
  }
}

.speaker-area {
  background: #ebebeb;
}

.speaker {
  display: flex;
  flex-wrap: wrap;
}

.speaker .single {
  position: relative;
  width: calc(100% / 4 - 30px);
  margin: 0 15px;
  text-align: center;
  margin-bottom: 25px;
}

.speaker .single .image {
  position: relative;
  padding: 25px;
  z-index: 1;
}

.speaker .single .image::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: url(data:image/png;base64,);
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  z-index: 0;
}

.speaker .single img {
  position: relative;
  width: 100%;
  border-radius: 100%;
  border: solid 5px #ecdb43;
  padding: 10px;
  z-index: 1;
}

.speaker .single .info {
  margin-top: 15px;
}

.speaker .single .info h4 {
  font-size: 26px;
}

.speaker .single .info p {
  font-weight: 300;
}

.speaker .single .image img:hover {
  -webkit-animation: moveImage 1s alternate;
          animation: moveImage 1s alternate;
}

@-webkit-keyframes moveImage {
  0% {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  } 33% {
    -webkit-transform: translate3d(-5px, 5px, 0);
            transform: translate3d(-5px, 5px, 0);
  } 66% {
    -webkit-transform: translate3d(5px, -5px, 0);
            transform: translate3d(5px, -5px, 0);
  } 100% {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
}

@keyframes moveImage {
  0% {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  } 33% {
    -webkit-transform: translate3d(-5px, 5px, 0);
            transform: translate3d(-5px, 5px, 0);
  } 66% {
    -webkit-transform: translate3d(5px, -5px, 0);
            transform: translate3d(5px, -5px, 0);
  } 100% {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
}

@media (max-width: 991px) {
  .speaker .single {
    width: calc(100% / 3 - 30px);
  }
}

@media (max-width: 768px) {
  .speaker .single {
    width: calc(100% / 2 - 30px);
  }
}

@media (max-width: 480px) {
  .speaker .single {
    width: calc(100% - 30px);
    text-align: center;
  }
}

table {
  width: 100%;
  border-collapse: collapse;
}

tbody th {
  text-align: left;
}

tbody>tr>* {
  padding: 15px 10px;
  border: solid 1px #ebebeb;
}

tbody tr th {
  background: #045fa4;
  color: #fff;
  border: none;
}

tbody tr .no {
  text-align: center;
}

tbody tr .time {
  font-weight: 700;
  color: #045fa4;
}

tbody tr th,
tbody tr .no {
  font-weight: 700;
}

tbody tr:hover {
  background: #ebebeb;
}

.sponsor-area {
  position: relative;
  background: #ecdb43;
}

.sponsor-area .container {
  position: relative;
  z-index: 1;
}

.sponsor-area::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 20px;
  top: -20px;
  background-image: url(/static/media/wave.769cd057.svg);
}

.sponsor {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.sponsor .single {
  width: calc(100%/3 - 30px);
  padding: 45px 25px;
  margin: 0 15px;
  text-align: center;
}

.sponsor .single img {
  width: calc(100% - 50px);
  -webkit-transition: 2s all;
  transition: 2s all;
}

@media (max-width: 991px) {
  .sponsor .single {
    padding: 45px 0px;
    padding-top: 0;
  }
}

@media (max-width: 991px) {
  .sponsor .single {
    padding: 25px 0px;
    padding-top: 0;
  }
}

@media (max-width: 480px) {
  .sponsor .single {
    width: calc(100%/2 - 30px);
  }
}

.footer-area {
  background: #2e2e2e;
  text-align: center;
  color: #fff;
}

.footer {
  width: 640px;
  margin: 0 auto;
}

.footer .logo {
  margin: 0 auto;
  margin-bottom: 25px;
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
}

.footer ul {
  padding: 25px 0;
}

.footer ul li {
  display: inline-block;
  margin: 0 5px;
}

.footer ul li a {
  color: #fff;
  text-decoration: none;
}

.event {
  padding: 25px 0;
  border-bottom: solid 3px #fff;
}

.event h3 {
  color: #fff;
  font-size: 48px;
}

@media (max-width: 768px) {
  .footer {
    width: 100%;
    padding: 0 25px;
  }

  .footer .logo {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }

  .event h3 {
    font-size: 36px;
  }
}

@media (max-width: 480px) {
}


